/* index.css */

body {
  margin: 0;
  padding: 0;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  background-color: #f9f9f9;
  color: #222;
}

header, footer {
  text-align: center;
  padding: 1rem;
  background-color: #4caf50;
  color: white;
}

button {
  padding: 0.5rem 1rem;
  margin: 1rem;
  font-size: 1rem;
  cursor: pointer;
  background-color: #1976d2;
  color: white;
  border: none;
  border-radius: 5px;
}

canvas {
  display: block;
  margin: 20px auto;
  border: 2px solid #555;
}

.stats-panel {
  padding: 1rem;
  background: #eee;
  border: 1px solid #ccc;
  margin: 20px;
}
